const ConsultaDocumentosScreen = props => {
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;

  const DateToUtcString = aDate => {
    return CustomCode.DateToUtcString(aDate);
  };

  const InitializeCurrentDate = () => {
    return CustomCode.InitializeCurrentDate(Constants);
  };

  const extractDate = sDate => {
    return CustomCode.FormatIsoString(sDate, 'DD/MM');
  };

  const date2HHMM = aDate => {
    return CustomCode.FormatIsoString(aDate, 'DD/MM HH:mm');
  };

  const renderIcon = docType => {
    switch (docType) {
      case 'Certificado':
        return (
          <Icon name={'Ionicons/receipt-outline'} size={24} color="white" />
        );

      case 'Recibo':
        return <Icon name={'FontAwesome/envelope-o'} size={24} color="white" />;

      default:
        return (
          <Icon
            name={'Ionicons/document-text-outline'}
            size={24}
            color="white"
          />
        );
    }
  };

  const log = value => {
    // return CustomCode.log(value);
  };

  const setShow = what => {
    setShowing(what);

    calcDateFromByOption(what);

    // console.log(`setShow(${what})`);
    CustomCode.log(`setShow(${what})`);
  };

  const calcDateFromByOption = showOption => {
    let rslt = new Date();

    console.log(`calcDateFromByOption(${showOption}): ${rslt}`);
    switch (showOption) {
      case 0:
        /// To start of month
        rslt.setDate(1);
        break;

      case 1:
        /// To start of year
        rslt.setMonth(0, 1);
        break;

      case 2:
        /// To start of age
        rslt = new Date(2000, 0, 1);
        break;
    }

    setDateFrom(rslt);
    console.log(`calcDateFromByOption(${showOption}): ${rslt}`);
  };

  const showDocument = item => {
    props.navigation.navigate('DetalleDocumentoScreen', {
      docType: item?.doc_type,
      docName: item?.doc_name,
      docDate: item?.doc_date,
      docDesc: item?.doc_description,
      docUrl: item?.doc_location,
      // docUrl: 'https://pbs.twimg.com/media/ES_4UzJXQAMHNrp.jpg'
    });

    // Recibo de sueldo https://pbs.twimg.com/media/ES_4UzJXQAMHNrp.jpg
    // Certificado https://cdn.eldoce.tv/sites/default/files/styles/site_nota_slider_preview/public/certificado-diego-maradona-agustina-cosachov.jpg
  };
  // to use a global variable
  const variables = CustomCode.useValues();
  // to update or modify the value of a global variable
  const setVariable = CustomCode.useSetValue();

  const DropDownHolder = CustomCode.DropDownHolder;

  const animationIsRunning = CustomCode.useRef(false);

  // const Nophoto = Images.Nophoto;
  // ["Ninguno","Vehiculo","Moto","Bici","Publico"]
  const { theme } = props;

  const {
    loading: screenLoading,
    data: screenData,
    error: screenError,
  } = DcSSApi.useConceptsByEmpGET({ emp_id: Constants['my_emp_id'] });

  const [dateFrom, setDateFrom] = React.useState(InitializeCurrentDate("''"));
  const [daySheet, setDaySheet] = React.useState([]);
  const [itemWidth, setItemWidth] = React.useState(0);
  const [refetchNum, setRefetchNum] = React.useState(1);
  const [requestsType, setRequestsType] = React.useState('E');
  const [screenName, setScreenName] = React.useState('Documentos');
  const [showing, setShowing] = React.useState(0);
  const [swipingLeft, setSwipingLeft] = React.useState(false);
  const [swipingRight, setSwipingRight] = React.useState(false);
  if (screenLoading) {
    return (
      <View style={{ flex: 1, justifyContent: 'center', alignItems: 'center' }}>
        <ActivityIndicator />
      </View>
    );
  }

  if (screenError) {
    return null;
  }

  if (!screenData) {
    return null;
  }

  return (
    <ScreenContainer
      style={styles.screen}
      scrollable={false}
      hasSafeArea={true}
    >
      <Utils.CustomCodeErrorBoundary>
        <CustomCode.DigicardScreenHeader
          {...props}
          right={{
            icon: 'Ionicons/home-outline',
            nav: 'Main',
            screen: 'InicioScreen',
          }}
          title={screenName}
        />
      </Utils.CustomCodeErrorBoundary>
      <View style={styles.Viewr9}>
        <CustomCode.rnSegmentedControlTab
          values={['Este Mes', 'Este Año', 'Todos']}
          onTabPress={index => setShow(index)}
          selectedIndex={showing}
        />
      </View>

      <View>
        <DcSSApi.FetchDocumentsByEmpGET
          date_from={DateToUtcString(dateFrom)}
          emp_id={Constants['my_emp_id']}
        >
          {({ loading, error, data, refetchDocumentsByEmp }) => {
            const fetchData = data;
            if (!fetchData || loading) {
              return <ActivityIndicator />;
            }

            if (error) {
              return (
                <Text style={{ textAlign: 'center' }}>
                  There was a problem fetching this data
                </Text>
              );
            }

            return (
              <View style={styles.ViewJh}>
                <FlatList
                  data={fetchData}
                  listKey={'KOKYXN0N'}
                  keyExtractor={({ item }) => item?.id || item?.uuid || item}
                  renderItem={({ item }) => {
                    const listData = item;
                    return (
                      <Touchable
                        onPress={() => {
                          try {
                            showDocument(listData);
                          } catch (err) {
                            console.error(err);
                          }
                        }}
                      >
                        <Surface
                          style={[
                            styles.SurfaceNZ,
                            {
                              borderRadius: 8,
                              backgroundColor: theme.colors.light,
                            },
                          ]}
                        >
                          <View style={[styles.View_1i, { borderRadius: 8 }]}>
                            <View
                              style={[
                                styles.ViewKZ,
                                {
                                  borderTopLeftRadius: 8,
                                  borderBottomLeftRadius: 8,
                                },
                              ]}
                            >
                              <Utils.CustomCodeErrorBoundary>
                                {renderIcon(listData?.doc_type)}
                              </Utils.CustomCodeErrorBoundary>
                            </View>

                            <View
                              style={[
                                styles.ViewvC,
                                {
                                  borderTopRightRadius: 8,
                                  borderBottomRightRadius: 8,
                                },
                              ]}
                            >
                              <View style={styles.ViewK3}>
                                <View style={styles.View_4g}>
                                  <Text
                                    style={[
                                      styles.TextLZ,
                                      { color: theme.colors.strong },
                                    ]}
                                  >
                                    {listData?.doc_name}
                                  </Text>
                                </View>

                                <View style={styles.ViewaO}>
                                  <Text
                                    style={[
                                      styles.TextZc,
                                      { color: theme.colors.strong },
                                    ]}
                                  >
                                    {date2HHMM(listData?.doc_date)}
                                  </Text>
                                </View>
                              </View>

                              <View style={styles.View_8b}>
                                <Text style={{ color: theme.colors.strong }}>
                                  {listData?.doc_description}
                                </Text>
                              </View>
                            </View>
                          </View>
                        </Surface>
                      </Touchable>
                    );
                  }}
                  contentContainerStyle={styles.FlatListKOContent}
                  numColumns={1}
                />
              </View>
            );
          }}
        </DcSSApi.FetchDocumentsByEmpGET>
      </View>
    </ScreenContainer>
  );
};

