  const getVersion = () => {
    var app;

    try {
      app = require('../app.json');
    } catch {
      app = undefined;
    }

    if (app) return app.expo.version;
    else return CustomCode.ExpoConstants.nativeAppVersion;
  };
  // to use a global variable
  const variables = CustomCode.useValues();
  // to update or modify the value of a global variable
  const setVariable = CustomCode.useSetValue();
  const { theme } = props;
