const NotificacionesScreen = props => {
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;

  const IsoString2ShortDate = utcString => {
    return CustomCode.IsoString2ShortDate(utcString);
  };

  const reqStatus2String = reqStatus => {
    const reqStatuses = {
      E: 'En proceso',
      A: 'Aprobada',
      D: 'Denegada',
      T: 'Todas',
    };

    return reqStatuses[reqStatus];
  };

  const log = value => {
    return CustomCode.log(value);
  };

  const setShow = what => {
    setShowing(what);

    calcFilter(what);

    // console.log(`setShow(${what})`);
    CustomCode.log(`setShow(${what})`);
  };

  const friendlyDate = aDate => {
    return CustomCode.Date2String(
      CustomCode.IsoDateStringToDate(aDate),
      'DD/MM HH:mm'
    );
  };

  const calcFilter = showOption => {
    // console.log(`calcFilter(${showOption})`);
    CustomCode.log(`calcFilter(${showOption})`);
    switch (showOption) {
      case 0:
        setRequestsType('N');
        break;

      case 1:
        setRequestsType('R');
        break;

      case 2:
        setRequestsType('A');
        break;
    }
  };

  const reqTypeToString = reqType => {
    const reqTypes = {
      vac: 'Vacaciones',
      ctu: 'Cambio de turno',
      pya: 'Permisos y ausencias',
      rys: 'Retiros y salidas intermedias',
      epp: 'EPP',
    };

    return reqTypes[reqType];
  };

  // Convierte una fecha en formato YYYY-MM-DD a Date
  const IsoDateStringToUTCDate = sDate => {
    return CustomCode.IsoDateStringToUTCDate(aDate);
  };

  const now = () => {
    return new Date().toISOString();
  };
  // to use a global variable
  const variables = CustomCode.useValues();
  // to update or modify the value of a global variable
  const setVariable = CustomCode.useSetValue();

  const DropDownHolder = CustomCode.DropDownHolder;

  // const Nophoto = Images.Nophoto;
  // ["Ninguno","Vehiculo","Moto","Bici","Publico"]
  const { theme } = props;

  const notificationPATCH = DcSSApi.useNotificationPATCH();

  const [daySheet, setDaySheet] = React.useState([]);
  const [requestsType, setRequestsType] = React.useState('E');
  const [screenName, setScreenName] = React.useState('Notificaciones');
  const [showing, setShowing] = React.useState(0);

  return (
    <ScreenContainer
      style={styles.screen}
      scrollable={false}
      hasSafeArea={true}
    >
      <Utils.CustomCodeErrorBoundary>
        <CustomCode.DigicardScreenHeader
          {...props}
          right={{
            icon: 'Ionicons/home-outline',
            nav: 'Main',
            screen: 'InicioScreen',
          }}
          title={screenName}
        />
      </Utils.CustomCodeErrorBoundary>
      <View style={styles.ViewqJ}>
        <CustomCode.rnSegmentedControlTab
          values={['Nuevas', 'Leidas', 'Todas']}
          onTabPress={index => setShow(index)}
          selectedIndex={showing}
        />
      </View>

      <ScrollView style={styles.ScrollView_87} bounces={true}>
        <DcSSApi.FetchNotificationsByEmpGET
          emp_id={Constants['my_emp_id']}
          limit={100}
          status={requestsType}
        >
          {({ loading, error, data, refetchNotificationsByEmp }) => {
            const fetchData = data;
            if (!fetchData || loading) {
              return <ActivityIndicator />;
            }

            if (error) {
              return (
                <Text style={{ textAlign: 'center' }}>
                  There was a problem fetching this data
                </Text>
              );
            }

            return (
              <View style={styles.ViewLu}>
                <FlatList
                  data={fetchData}
                  listKey={'1vBv0LIP'}
                  keyExtractor={({ item }) => item?.id || item?.uuid || item}
                  renderItem={({ item }) => {
                    const itemsListData = item;
                    return (
                      <Touchable
                        onPress={async () => {
                          try {
                            if (itemsListData?.not_readed) {
                              return;
                            }
                            console.log('No leida');
                            await notificationPATCH.mutateAsync({
                              not_id: itemsListData?.not_id,
                              read: true,
                              read_date: now(),
                            });
                            await refetchNotificationsByEmp();
                          } catch (err) {
                            console.error(err);
                          }
                        }}
                        style={styles.Touchable_5n}
                      >
                        <Surface
                          style={[
                            styles.SurfacebA,
                            {
                              borderRadius: 8,
                              backgroundColor: theme.colors.very_light,
                            },
                          ]}
                          elevation={3}
                        >
                          <View>
                            <View style={styles.View_1r}>
                              <Text
                                style={[
                                  styles.TextXB,
                                  { color: theme.colors.strong },
                                ]}
                              >
                                {itemsListData?.not_subject}
                              </Text>

                              <View style={styles.ViewDU}>
                                <>
                                  {!itemsListData?.not_read ? null : (
                                    <Icon
                                      style={styles.IconHh}
                                      name={'AntDesign/check'}
                                      size={18}
                                      color={theme.colors.rigth}
                                    />
                                  )}
                                </>
                              </View>
                            </View>

                            <Text
                              style={[
                                styles.TextZZ,
                                { color: theme.colors.strong },
                              ]}
                              ellipsizeMode={'clip'}
                              numberOfLines={1}
                            >
                              {itemsListData?.not_body}
                            </Text>
                          </View>

                          <View style={styles.Viewoc}>
                            <Text
                              style={[
                                styles.TextR1,
                                { color: theme.colors.strong },
                              ]}
                            >
                              {itemsListData?.not_from_emp_name}
                            </Text>
                            <>
                              {!itemsListData?.not_date ? null : (
                                <Text
                                  style={[
                                    styles.Textb3,
                                    { color: theme.colors.strong },
                                  ]}
                                >
                                  {friendlyDate(itemsListData?.not_date)}
                                </Text>
                              )}
                            </>
                          </View>
                        </Surface>
                      </Touchable>
                    );
                  }}
                  numColumns={1}
                />
              </View>
            );
          }}
        </DcSSApi.FetchNotificationsByEmpGET>
      </ScrollView>
    </ScreenContainer>
  );
};

