import React from 'react';
import * as CustomCode from '../components.js';
import * as GlobalVariables from '../config/GlobalVariableContext';
import * as Utils from '../utils';
import {
  CircleImage,
  Container,
  Icon,
  ScreenContainer,
  Touchable,
  withTheme,
} from '@draftbit/ui';
import { StyleSheet, Text, View } from 'react-native';

const YYYOpcionesScreen = props => {
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;

  const { theme } = props;
  const { navigation } = props;

  const [showDrawer, setShowDrawer] = React.useState(false);

  return (
    <ScreenContainer hasSafeArea={true} hasTopSafeArea={true}>
      <Utils.CustomCodeErrorBoundary>
        <CustomCode.DigicardScreenHeader
          {...props}
          left={{
            icon: 'MaterialCommunityIcons/backburger',
            onPress: () => setShowDrawer(true),
            nav: 'Main',
            screen: 'InicioScreen',
          }}
        />
      </Utils.CustomCodeErrorBoundary>
      <View>
        <CustomCode.rnModal
          isVisible={showDrawer}
          animationIn={'slideInLeft'}
          animationOut={'slideOutLeft'}
          backdropColor={'white'}
          backdropOpacity={0.1}
          onBackdropPress={() => setShowDrawer(false)}
          onSwipeComplete={() => setShowDrawer(false)}
          swipeDirection={'left'}
          swipeThreshold={50}
          style={{ margin: 0 }}
          hideModalContentWhileAnimating={true}
        >
          <Container
            style={[
              styles.ContainerZ4,
              {
                backgroundColor: theme.colors.light,
                borderRadius: 0,
                borderTopRightRadius: 16,
                borderBottomRightRadius: 16,
              },
            ]}
            backgroundImageResizeMode={'cover'}
          >
            <View style={styles.ViewMi}>
              <CircleImage
                size={80}
                source={{ uri: `${Constants['emp_photo']}` }}
              />
            </View>

            <Container
              style={[styles.ContainerXK, { borderRadius: 1 }]}
              backgroundImageResizeMode={'cover'}
            >
              <View style={{ borderRadius: 23 }}>
                <Touchable
                  onPress={() => {
                    try {
                      setShowDrawer(false);
                    } catch (err) {
                      console.error(err);
                    }
                  }}
                  style={styles.TouchablebZ}
                >
                  <Icon
                    name={'MaterialCommunityIcons/backburger'}
                    size={24}
                    color={theme.colors.background}
                  />
                </Touchable>

                <Touchable
                  onPress={() => {
                    try {
                      navigation.navigate('Main', { screen: 'AboutScreen' });
                    } catch (err) {
                      console.error(err);
                    }
                  }}
                  style={styles.TouchableOs}
                >
                  <Text
                    style={[styles.TextYQ, { color: theme.colors.background }]}
                  >
                    {'Acerca de'}
                  </Text>
                </Touchable>

                <Touchable
                  onPress={() => {
                    try {
                      navigation.navigate('Main');
                    } catch (err) {
                      console.error(err);
                    }
                  }}
                  style={styles.TouchablebA}
                >
                  <Text
                    style={[styles.Textvs, { color: theme.colors.background }]}
                  >
                    {'Cerrar sesión'}
                  </Text>
                </Touchable>
              </View>
            </Container>
          </Container>
        </CustomCode.rnModal>
      </View>
    </ScreenContainer>
  );
};

const styles = StyleSheet.create({
  ViewMi: {
    position: 'absolute',
    zIndex: 99,
    right: 5,
    top: 5,
  },
  TouchablebZ: {
    marginTop: 5,
    marginBottom: 5,
  },
  TextYQ: {
    fontSize: 18,
    fontFamily: 'System',
    fontWeight: '500',
  },
  TouchableOs: {
    marginTop: 25,
    marginBottom: 5,
  },
  Textvs: {
    fontSize: 18,
    fontFamily: 'System',
    fontWeight: '500',
    marginBottom: 10,
  },
  TouchablebA: {
    marginTop: 5,
    marginBottom: 5,
  },
  TextlF: {
    fontSize: 18,
    fontFamily: 'System',
    fontWeight: '500',
  },
  Touchable_10: {
    marginTop: 5,
    marginBottom: 5,
  },
  ContainerXK: {
    height: '100%',
    width: '70%',
  },
  ContainerZ4: {
    width: '85%',
    alignSelf: 'flex-start',
    flex: 1,
    paddingTop: 8,
    paddingLeft: 29,
    opacity: 0.85,
  },
});

export default withTheme(YYYOpcionesScreen);
