import React from 'react';
import * as DcSSApi from '../apis/DcSSApi.js';
import * as CustomCode from '../components.js';
import * as GlobalVariables from '../config/GlobalVariableContext';
import * as Utils from '../utils';
import {
  ButtonSolid,
  ScreenContainer,
  Touchable,
  withTheme,
} from '@draftbit/ui';
import { useIsFocused } from '@react-navigation/native';
import { Image, StyleSheet, Text, View } from 'react-native';

const CompletaInicioSessionScreen = props => {
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;

  const setGlobalVariableValue = GlobalVariables.useSetValue();
  const showVars = () => {
    const platform = CustomCode.Platform.OS;
    console.log(`platform: ${platform}`);

    // const constants = CustomCode.ExpoConstants[CustomCode.Platform.OS];
    const constants = CustomCode.ExpoConstants;
    console.log(`constants: ${JSON.stringify(constants)}`);

    const isDevice = CustomCode.Platform.OS !== 'web' && constants.isDevice;
    console.log(`isDevice: ${isDevice}`);
  };

  const getNotificationToken = async () => {
    const isDevice =
      CustomCode.Device.isDevice && CustomCode.Platform.OS !== 'web'; // && CustomCode.ExpoConstants;

    if (isDevice && CustomCode.Device.isDevice) {
      const expoPushToken =
        await CustomCode.ExpoNotifications.getExpoPushTokenAsync({
          experienceId: '@digicard/dss',
        });

      console.log(`expoPushToken: $expoPushToken}`);

      return expoPushToken;
    } else return { data: '' };
  };

  const { theme } = props;
  const { navigation } = props;

  const employeeTokenPATCH = DcSSApi.useEmployeeTokenPATCH();

  const isFocused = useIsFocused();
  React.useEffect(async () => {
    try {
      if (!isFocused) {
        return;
      }
      const expoToken = await getNotificationToken();
      setGlobalVariableValue({
        key: 'ExpoNotifToken',
        value: expoToken,
      });
      const tokenData = expoToken.data;
      await employeeTokenPATCH.mutateAsync({
        emp_id: Constants['my_emp_id'],
        emp_token: tokenData,
      });
      if (false) {
        return;
      }
      navigation.navigate('Main', { screen: 'InicioScreen' });
    } catch (err) {
      console.error(err);
    }
  }, [isFocused]);

  return (
    <ScreenContainer
      style={[styles.screen, { backgroundColor: theme.colors.background }]}
      scrollable={false}
      hasSafeArea={true}
    >
      <Utils.CustomCodeErrorBoundary>
        <CustomCode.DigicardScreenHeader {...props} left={{}} />
      </Utils.CustomCodeErrorBoundary>
      <View style={styles.ViewKd}>
        <Image style={styles.Imagebw} resizeMode={'contain'} />
        <Text
          style={[styles.Text_6W, { color: theme.colors.strong }]}
          textBreakStrategy={'highQuality'}
          ellipsizeMode={'tail'}
          allowFontScaling={true}
        >
          {'Registrando Notificaciones...'}
        </Text>

        <Text
          style={[styles.TextYb, { color: theme.colors.strong }]}
          textBreakStrategy={'highQuality'}
          ellipsizeMode={'tail'}
          allowFontScaling={true}
        >
          {'Un momento por favor...'}
        </Text>
      </View>
    </ScreenContainer>
  );
};

const styles = StyleSheet.create({
  Imagebw: {
    height: 150,
    width: 150,
    marginBottom: 16,
    overflow: 'hidden',
  },
  Text_6W: {
    fontSize: 28,
    alignSelf: 'center',
    fontFamily: 'System',
    fontWeight: '700',
    textAlign: 'center',
  },
  TextYb: {
    fontSize: 16,
    textAlign: 'center',
    lineHeight: 28,
    alignSelf: 'center',
    marginLeft: '3%',
    marginRight: '3%',
  },
  ViewKd: {
    alignItems: 'center',
    flex: 1,
    justifyContent: 'center',
  },
  TextOd: {
    textAlign: 'center',
    textTransform: 'uppercase',
    opacity: 0.51,
  },
  Touchableof: {
    marginBottom: '8%',
  },
  ButtonSolidLm: {
    borderRadius: 10,
    textAlign: 'center',
    width: '100%',
    marginLeft: '6%',
    marginRight: '6%',
    minHeight: 48,
    fontFamily: 'System',
    fontWeight: '700',
  },
  Viewco: {
    marginLeft: '6%',
    marginRight: '6%',
    marginBottom: '10%',
    justifyContent: 'flex-end',
    alignItems: 'center',
    alignContent: 'center',
  },
  screen: {
    justifyContent: 'space-between',
  },
});

export default withTheme(CompletaInicioSessionScreen);
