import React from 'react';
import * as DcSSApi from '../apis/DcSSApi.js';
import * as CustomCode from '../components.js';
import * as GlobalVariables from '../config/GlobalVariableContext';
import * as Utils from '../utils';
import {
  ButtonSolid,
  Container,
  ScreenContainer,
  withTheme,
} from '@draftbit/ui';
import { useIsFocused } from '@react-navigation/native';
import { ActivityIndicator, StyleSheet, Text, View } from 'react-native';
import { Fetch } from 'react-request';

const BienvenidoScreen = props => {
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;

  const stopIfNonZero = value => {
    // const variables = CustomCode.useValues();
    const perms_got = Variables['perms_got']; //

    console.log(`value: ${value}, perms_got: ${perms_got}`);

    return value == 0 ? 1 : 0;
  };

  const HasAllPermissions = async () => {
    if (CustomCode.Platform.OS === 'web') return true;

    // Camera
    const { status } = await CustomCode.Camera.getCameraPermissionsAsync();

    console.log(`Camera Permission status: ${status}`);

    if (status !== 'granted') return false;

    // Notifications
    const settings = await CustomCode.ExpoNotifications.getPermissionsAsync();
    console.log(
      `Notifications Permission status: ${settings.status}, ios: ${settings?.ios}`
    );

    return (
      settings.granted ||
      settings.ios?.status ===
        CustomCode.ExpoNotifications.IosAuthorizationStatus.AUTHORIZED
    );
  };
  // to use a global variable
  const variables = CustomCode.useValues();
  // to update or modify the value of a global variable
  const setVariable = CustomCode.useSetValue();
  const { theme } = props;
  const { navigation } = props;

  const isFocused = useIsFocused();
  React.useEffect(async () => {
    try {
      if (!isFocused) {
        return;
      }
      if (false) {
        return;
      }
      console.log(Constants['my_emp_id']);
      const hasPermissions = await HasAllPermissions();
      if (!hasPermissions) {
        return;
      }
      console.log('Not stopped');
      navigation.navigate('Permissions', {
        screen: 'CompletaInicioSessionScreen',
      });
    } catch (err) {
      console.error(err);
    }
  }, [isFocused]);

  const [screenName, setScreenName] = React.useState(undefined);

  return (
    <ScreenContainer
      style={styles.screen}
      scrollable={false}
      hasSafeArea={true}
    >
      <Utils.CustomCodeErrorBoundary>
        <CustomCode.DigicardScreenHeader
          {...props}
          left={{}}
          title={screenName}
        />
      </Utils.CustomCodeErrorBoundary>
      <View style={styles.Viewnn}>
        <View style={styles.ViewaB}>
          <DcSSApi.FetchEmployeeConfigGET emp_id={Constants['my_emp_id']}>
            {({ loading, error, data, refetchEmployeeConfig }) => {
              const fetchData = data;
              if (!fetchData || loading) {
                return <ActivityIndicator />;
              }

              if (error) {
                return (
                  <Text style={{ textAlign: 'center' }}>
                    There was a problem fetching this data
                  </Text>
                );
              }

              return (
                <>
                  <View style={styles.ViewyX}>
                    <Text
                      style={[styles.TextyP, { color: theme.colors.secondary }]}
                    >
                      {fetchData?.companyName}
                    </Text>
                  </View>

                  <Container
                    style={[
                      styles.ContainerfU,
                      { borderColor: theme.colors.medium },
                    ]}
                    elevation={0}
                    useThemeGutterPadding={true}
                  >
                    <Text
                      style={[
                        styles.TextOB,
                        {
                          color: theme.colors.strong,
                          textDecorationColor: theme.colors.custom_rgb255_0_0,
                        },
                      ]}
                    >
                      {'Bienvenido '}
                      {fetchData?.emp_first_name}
                      {'!\n'}
                    </Text>
                  </Container>
                </>
              );
            }}
          </DcSSApi.FetchEmployeeConfigGET>
          <Text style={[styles.TextU3, { color: theme.colors.strong }]}>
            {
              'A continuación se le solicitarán ciertos permisos a otorgar para el funcionamiento correcto de la aplicación'
            }
          </Text>

          <View style={styles.ViewH4}>
            <Text
              style={[
                styles.TextZH,
                {
                  color: theme.colors.strong,
                  textDecorationColor: theme.colors.custom_rgb255_0_0,
                },
              ]}
            >
              {'Seleccione iniciar para continuar\n'}
            </Text>
          </View>
        </View>
      </View>

      <View style={styles.View_8j}>
        <ButtonSolid
          onPress={() => {
            try {
              console.log(Constants['perms_got']);
              navigation.navigate('Permissions', {
                screen: 'PermisoDeNotificacionesScreen',
              });
            } catch (err) {
              console.error(err);
            }
          }}
          style={[
            styles.ButtonSolid_3O,
            {
              backgroundColor: theme.colors.primary,
              color: theme.colors.background,
            },
          ]}
          title={'Iniciar'}
        />
      </View>
    </ScreenContainer>
  );
};

