export const comedor2ByEmpGET = (Constants, { date_from, emp_id }) =>
  fetch(
    `https://dcss-api.herokuapp.com/timesheets_orders/emp/${
      emp_id ?? ''
    }?filter={
  "limit": 100,
  "order": "tio_date Desc",
  "where": {
    "tio_date": {"gt": "${date_from ?? ''}"} 
  }
}`,
    {
      headers: {
        Accept: 'application/json',
        Authorization: Constants['AUTH_HEADER'],
        'Content-Type': 'application/json',
      },
    }
  )
    .then(res => {
      if (!res.ok) {
        console.error('Fetch error: ' + res.status + ' ' + res.statusText);
      }
      return res;
    })
    .then(res => res.json())
    .catch(() => {});

export const useComedor2ByEmpGET = (args, { refetchInterval } = {}) => {
  const Constants = GlobalVariables.useValues();
  return useQuery(
    ['comedor2s', args],
    () => comedor2ByEmpGET(Constants, args),
    {
      refetchInterval,
    }
  );
};

export const FetchComedor2ByEmpGET = ({
  children,
  onData = () => {},
  refetchInterval,
  date_from,
  emp_id,
}) => {
  const Constants = GlobalVariables.useValues();
  const isFocused = useIsFocused();
  const prevIsFocused = usePrevious(isFocused);

  const { loading, data, error, refetch } = useComedor2ByEmpGET(
    { date_from, emp_id },
    { refetchInterval }
  );

  React.useEffect(() => {
    if (!prevIsFocused && isFocused) {
      refetch();
    }
  }, [isFocused, prevIsFocused]);

  React.useEffect(() => {
    if (error) {
      console.error('Fetch error: ' + error.status + ' ' + error.statusText);
      console.error(error);
    }
  }, [error]);
  React.useEffect(() => {
    if (data) {
      onData(data);
    }
  }, [data]);

  return children({ loading, data, error, refetchComedor2ByEmp: refetch });
};

