export const employeePATCH = (
  Constants,
  {
    address,
    birth_date,
    contact,
    emp_id,
    emp_photo,
    first_name,
    health,
    last_name,
    mail,
    phone,
    transport,
  }
) =>
  fetch(`https://dcss-api.herokuapp.com/employees/${emp_id ?? ''}`, {
    body: JSON.stringify({
      emp_first_name: first_name,
      emp_last_name: last_name,
      emp_birth_date: birth_date,
      emp_mail: mail,
      emp_phone: phone,
      emp_address: address,
      emp_contact: contact,
      emp_health: health,
      emp_transport: transport,
      emp_photo: emp_photo,
    }),
    headers: {
      Accept: 'application/json',
      Authorization: Constants['AUTH_HEADER'],
      'Content-Type': 'application/json',
    },
    method: 'PATCH',
  })
    .then(res => {
      if (!res.ok) {
        console.error('Fetch error: ' + res.status + ' ' + res.statusText);
      }
      return res;
    })
    .then(res => res.json())
    .catch(() => {});

export const useEmployeePATCH = initialArgs => {
  const queryClient = useQueryClient();
  const Constants = GlobalVariables.useValues();

  return useMutation(
    args => employeePATCH(Constants, { ...initialArgs, ...args }),
    {
      onError: (err, variables, { previousValue }) => {
        if (previousValue) {
          return queryClient.setQueryData('employee', previousValue);
        }
      },
      onSettled: () => {
        queryClient.invalidateQueries('employee');
        queryClient.invalidateQueries('employees');
      },
    }
  );
};

