import * as React from 'react';
import AppNavigator from './AppNavigator2';
import DropdownAlert from 'react-native-dropdownalert';
import UserInactivity from 'react-native-user-inactivity';
import * as GlobalVariables from './config/GlobalVariableContext';
import * as CustomCode from './components.js';

export default function RootAppNavigator() {
  const INACTIVITY_TIMEOUT = 50000;
  const REACTION_TIMEOUT = 10000;
  const TESTING = false;

  const [inactivityWarning, setInactivityWarning] = React.useState(false);
  const [timer, setTimer] = React.useState(INACTIVITY_TIMEOUT);
  const [timeoutHandle, setTimeoutHandle] = React.useState(null);

  const setGlobalVariableValue = GlobalVariables.useSetValue();
  const Constants = GlobalVariables.useValues();

  React.useEffect(() => {

    if((TESTING || CustomCode.Platform.OS === 'web') && Constants['AUTH_HEADER']?.length) {
      if(inactivityWarning) {
        CustomCode.DropDownHolder?.getDropDown()?.alertWithType(
          'warn',
          '¿ Estás ahi ?',
          `No detectamos actividad, toca la pantalla en cualquier lado o tu sesión se cerrará en ${REACTION_TIMEOUT/1000} segundos`,
          null,
          REACTION_TIMEOUT
        );

        console.log('timer started');
        setTimeoutHandle(setTimeout(() => {
          setGlobalVariableValue({
            key: 'AUTH_HEADER',
            value: '',
          });

          CustomCode.getNavigator().navigate('Auth', {
            screen: 'IniciarSesionScreen',
          });
        }, REACTION_TIMEOUT));
      }
      else {
        clearTimeout(timeoutHandle);
        CustomCode.DropDownHolder?.getDropDown()?.close();
        console.log('timer cleared');
      }
    }
  }, 
  [inactivityWarning]);

  return (
    TESTING || CustomCode.Platform.OS === 'web' 
    ? (
      <UserInactivity
      isActive={true}
      timeForInactivity={timer}
      onAction={isActive => setInactivityWarning(!isActive)}
    >
       <DropdownAlert
        closeInterval={null}
        zIndex={10}
        ref={dropRef => {
          CustomCode.DropDownHolder.setDropDown(dropRef);
        }}
      />
      <AppNavigator />
    </UserInactivity>
    ) 
    : (
      <>
        <DropdownAlert
          closeInterval={null}
          zIndex={10}
          ref={dropRef => {
            CustomCode.DropDownHolder.setDropDown(dropRef);
          }}
          />
        <AppNavigator />
      </>
    )
  );
}
