import React from 'react';
import * as DcSSApi from '../apis/DcSSApi.js';
import * as CustomCode from '../components.js';
import * as GlobalVariables from '../config/GlobalVariableContext';
import Images from '../config/Images';
import * as Utils from '../utils';
import {
  ButtonSolid,
  ScreenContainer,
  Touchable,
  withTheme,
} from '@draftbit/ui';
import { useIsFocused } from '@react-navigation/native';
import { Image, StyleSheet, Text, View } from 'react-native';

const PermisoDeNotificacionesScreen = props => {
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;

  const setGlobalVariableValue = GlobalVariables.useSetValue();
  const askPermission = async () => {
    const { status, ios } =
      await CustomCode.ExpoNotifications.requestPermissionsAsync({
        ios: {
          allowAlert: true,
          allowBadge: true,
          allowSound: true,
          allowAnnouncements: true,
        },
      });

    console.log(`status: ${status}, ios: ${ios}`);

    if (CustomCode.Platform === 'ios')
      return (
        ios.status ==
        CustomCode.ExpoNotifications.IosAuthorizationStatus.AUTHORIZED
      );
    else return status === 'granted';
  };

  const showVars = () => {
    // console.log(`expo manifest.id: ${CustomCode.ExpoConstants}`);
    // console.log(`ExpoConstants: ${JSON.stringify(CustomCode.ExpoConstants)}`);
    // console.log(`expo manifest.id: ${JSON.stringify(CustomCode.ExpoConstants.manifest)}`);
    // console.log(`expo currentFullName: ${CustomCode.ExpoConstants.ExpoClientConfig.currentFullName}`);
  };

  const getNotificationToken = async () => {
    const expoPushToken =
      await CustomCode.ExpoNotifications.getExpoPushTokenAsync({
        experienceId: '@digicard/dss',
      });

    console.log(`expoPushToken: $expoPushToken}`);

    return expoPushToken;
  };

  const getPermission = async () => {
    const isDevice =
      CustomCode.Device.isDevice && CustomCode.Platform.OS !== 'web'; // && CustomCode.ExpoConstants;

    if (isDevice) {
      const settings = await CustomCode.ExpoNotifications.getPermissionsAsync();

      console.log(`status: ${settings.status}, ios: ${settings?.ios}`);

      return (
        settings.granted ||
        settings.ios?.status ===
          CustomCode.ExpoNotifications.IosAuthorizationStatus.AUTHORIZED
      );
    } else return true;
  };

  const { theme } = props;
  const { navigation } = props;

  const employeeTokenPATCH = DcSSApi.useEmployeeTokenPATCH();

  const isFocused = useIsFocused();
  React.useEffect(async () => {
    try {
      if (!isFocused) {
        return;
      }
      showVars();
      const hasPermission = await getPermission();
      if (!hasPermission) {
        return;
      }
      navigation.navigate('Permissions', { screen: 'PermisoDeCamaraScreen' });
    } catch (err) {
      console.error(err);
    }
  }, [isFocused]);

  return (
    <ScreenContainer
      style={[styles.screen, { backgroundColor: theme.colors.background }]}
      scrollable={false}
      hasSafeArea={true}
    >
      <Utils.CustomCodeErrorBoundary>
        <CustomCode.DigicardScreenHeader {...props} left={{}} />
      </Utils.CustomCodeErrorBoundary>
      <View style={styles.VieweX}>
        <Image
          style={styles.ImagewC}
          resizeMode={'contain'}
          source={Images.PermissionsNotification}
        />
        <Text
          style={[styles.TextBL, { color: theme.colors.strong }]}
          textBreakStrategy={'highQuality'}
          ellipsizeMode={'tail'}
          allowFontScaling={true}
        >
          {'Permitir Notificaciones'}
        </Text>

        <Text
          style={[styles.Text_79, { color: theme.colors.strong }]}
          textBreakStrategy={'highQuality'}
          ellipsizeMode={'tail'}
          allowFontScaling={true}
        >
          {
            'Manténgase informado de los últimos cambios y novedades en su empresa'
          }
        </Text>
      </View>

      <View style={styles.ViewNx}>
        <Touchable
          onPress={() => {
            try {
              navigation.navigate('Permissions', {
                screen: 'PermisoDeCamaraScreen',
              });
            } catch (err) {
              console.error(err);
            }
          }}
          style={styles.Touchable_2v}
        >
          <Text
            style={[
              theme.typography.button,
              styles.TextV5,
              { color: theme.colors.strong },
            ]}
            textBreakStrategy={'highQuality'}
            ellipsizeMode={'tail'}
            allowFontScaling={true}
          >
            {'Saltar'}
          </Text>
        </Touchable>
        <ButtonSolid
          onPress={async () => {
            try {
              setGlobalVariableValue({
                key: 'perms_got',
                value: 1,
              });
              const granted = await askPermission();
              if (!granted) {
                return;
              }
              const notificationToken = await getNotificationToken();
              await employeeTokenPATCH.mutateAsync({
                emp_id: Constants['my_emp_id'],
                emp_token: notificationToken,
              });
              navigation.navigate('Permissions', {
                screen: 'PermisoDeCamaraScreen',
              });
            } catch (err) {
              console.error(err);
            }
          }}
          style={[
            styles.ButtonSolidHl,
            {
              backgroundColor: theme.colors.primary,
              color: theme.colors.background,
            },
          ]}
          title={'Permitir Notificaciones'}
        />
      </View>
    </ScreenContainer>
  );
};

const styles = StyleSheet.create({
  ImagewC: {
    height: 150,
    width: 150,
    marginBottom: 16,
    overflow: 'hidden',
  },
  TextBL: {
    fontSize: 28,
    alignSelf: 'center',
    fontFamily: 'System',
    fontWeight: '700',
  },
  Text_79: {
    fontSize: 16,
    textAlign: 'center',
    lineHeight: 28,
    alignSelf: 'center',
    marginLeft: '3%',
    marginRight: '3%',
  },
  VieweX: {
    alignItems: 'center',
    justifyContent: 'center',
    alignSelf: 'center',
    alignContent: 'center',
  },
  TextV5: {
    textAlign: 'center',
    textTransform: 'uppercase',
    opacity: 0.51,
  },
  Touchable_2v: {
    marginBottom: '8%',
  },
  ButtonSolidHl: {
    borderRadius: 10,
    textAlign: 'center',
    width: '100%',
    marginLeft: '6%',
    marginRight: '6%',
    minHeight: 48,
    fontFamily: 'System',
    fontWeight: '700',
  },
  ViewNx: {
    marginLeft: '6%',
    marginRight: '6%',
    marginBottom: '10%',
    justifyContent: 'flex-end',
    alignItems: 'center',
    alignContent: 'center',
  },
  screen: {
    justifyContent: 'space-between',
  },
});

export default withTheme(PermisoDeNotificacionesScreen);
