import React from 'react';
import * as CustomCode from '../components.js';
import * as GlobalVariables from '../config/GlobalVariableContext';
import Images from '../config/Images';
import * as Utils from '../utils';
import {
  ButtonSolid,
  ScreenContainer,
  Touchable,
  withTheme,
} from '@draftbit/ui';
import { useIsFocused } from '@react-navigation/native';
import { Image, StyleSheet, Text, View } from 'react-native';

const PermisoDeCamaraScreen = props => {
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;

  const setGlobalVariableValue = GlobalVariables.useSetValue();
  const getPermission = async () => {
    if (CustomCode.Platform.OS === 'web') return true;

    const { status } = await CustomCode.Camera.getCameraPermissionsAsync();

    console.log(`status: ${status}`);

    return status === 'granted';
  };

  const askPermission = async () => {
    const { status } = await CustomCode.Camera.requestCameraPermissionsAsync();

    console.log(`status: ${status}`);

    return status === 'granted';
    // props.navigation.navigate(target);
  };

  const { theme } = props;
  const { navigation } = props;

  const isFocused = useIsFocused();
  React.useEffect(async () => {
    try {
      if (!isFocused) {
        return;
      }
      const hasPermission = await getPermission();
      if (!hasPermission) {
        return;
      }
      navigation.navigate('Permissions', {
        screen: 'CompletaInicioSessionScreen',
      });
    } catch (err) {
      console.error(err);
    }
  }, [isFocused]);

  return (
    <ScreenContainer
      style={[styles.screen, { backgroundColor: theme.colors.background }]}
      scrollable={false}
      hasSafeArea={true}
    >
      <Utils.CustomCodeErrorBoundary>
        <CustomCode.DigicardScreenHeader {...props} left={{}} />
      </Utils.CustomCodeErrorBoundary>
      <View style={styles.ViewiF}>
        <Image
          style={styles.Imageg6}
          resizeMode={'contain'}
          source={Images.PermissionsCamera}
        />
        <Text
          style={[styles.TextRN, { color: theme.colors.strong }]}
          textBreakStrategy={'highQuality'}
          ellipsizeMode={'tail'}
          allowFontScaling={true}
        >
          {'Permitir Cámara'}
        </Text>

        <Text
          style={[styles.Textvj, { color: theme.colors.strong }]}
          textBreakStrategy={'highQuality'}
          ellipsizeMode={'tail'}
          allowFontScaling={true}
        >
          {
            'Lo necesitaremos para tomar fotos, acceder al carrete de la cámara o grabar videos.\n'
          }
        </Text>
      </View>

      <View style={styles.ViewIf}>
        <Touchable
          onPress={() => {
            try {
              navigation.navigate('Permissions', {
                screen: 'CompletaInicioSessionScreen',
              });
            } catch (err) {
              console.error(err);
            }
          }}
          style={styles.TouchableRJ}
        >
          <Text
            style={[
              theme.typography.button,
              styles.TextYZ,
              { color: theme.colors.strong },
            ]}
            textBreakStrategy={'highQuality'}
            ellipsizeMode={'tail'}
            allowFontScaling={true}
          >
            {'Saltar'}
          </Text>
        </Touchable>
        <ButtonSolid
          onPress={async () => {
            try {
              setGlobalVariableValue({
                key: 'perms_got',
                value: 1,
              });
              const granted = await askPermission();
              if (!granted) {
                return;
              }
              navigation.navigate('Permissions', {
                screen: 'CompletaInicioSessionScreen',
              });
            } catch (err) {
              console.error(err);
            }
          }}
          style={[
            styles.ButtonSolidae,
            {
              backgroundColor: theme.colors.primary,
              color: theme.colors.background,
            },
          ]}
          title={'Permitir Cámara'}
        />
      </View>
    </ScreenContainer>
  );
};

const styles = StyleSheet.create({
  Imageg6: {
    marginBottom: 16,
    overflow: 'hidden',
    height: 150,
    width: 150,
  },
  TextRN: {
    fontSize: 28,
    alignSelf: 'center',
    fontFamily: 'System',
    fontWeight: '700',
  },
  Textvj: {
    fontSize: 16,
    textAlign: 'center',
    lineHeight: 28,
    alignSelf: 'center',
    marginLeft: '3%',
    marginRight: '3%',
  },
  ViewiF: {
    alignItems: 'center',
    justifyContent: 'center',
    alignSelf: 'center',
    alignContent: 'center',
  },
  TextYZ: {
    textAlign: 'center',
    textTransform: 'uppercase',
    opacity: 0.52,
  },
  TouchableRJ: {
    marginBottom: '8%',
  },
  ButtonSolidae: {
    borderRadius: 10,
    fontFamily: 'System',
    fontWeight: '700',
    textAlign: 'center',
    width: '100%',
    marginLeft: '6%',
    marginRight: '6%',
    minHeight: 48,
  },
  ViewIf: {
    marginLeft: '6%',
    marginRight: '6%',
    marginBottom: '10%',
    justifyContent: 'flex-end',
    alignItems: 'center',
    alignContent: 'center',
  },
  screen: {
    justifyContent: 'space-between',
  },
});

export default withTheme(PermisoDeCamaraScreen);
