import React from 'react';
import * as DcSSApi from '../apis/DcSSApi.js';
import * as CustomCode from '../components.js';
import * as GlobalVariables from '../config/GlobalVariableContext';
import Images from '../config/Images';
import * as Utils from '../utils';
import {
  ButtonSolid,
  DatePicker,
  ScreenContainer,
  withTheme,
} from '@draftbit/ui';
import { useIsFocused } from '@react-navigation/native';
import { Image, StyleSheet, Text, View } from 'react-native';

const SolicitudVacacionesScreen = props => {
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;

  const initScreenVars = () => {
    setDateFrom(undefined);
    setDateTo(undefined);
    setDateFromError(undefined);
    setDateToError(undefined);
    // setConcept(undefined);
    // setReason(undefined);
  };

  const now = () => {
    return new Date().toISOString();
  };

  const postResultToast = fetchResult => {
    return CustomCode.PostResultToast(
      fetchResult,
      screenName,
      'back',
      '',
      2000,
      props
    );
  };

  const validate = () => {
    setDateFromError(undefined);
    setDateToError(undefined);

    // console.log(`validate: dateFrom: ${dateFrom}, dateTo: ${dateTo}`);

    const minDate = new Date();
    minDate.setDate(1 + minDate.getDate());
    minDate.setHours(0, 0, 0, 0);

    if (dateFrom >= minDate)
      if (dateTo >= dateFrom) return false;
      else setDateToError("'Hasta' debe ser posterior a 'Desde'");
    else setDateFromError('No puede comenzar antes de mañana');

    return true;
  };

  // Convierte un Date a un string YYYY-MM-DD
  const Date2IsoDateString = aDate => {
    return CustomCode.Date2IsoDateString(aDate);
  };
  // to use a global variable
  const variables = CustomCode.useValues();
  // to update or modify the value of a global variable
  const setVariable = CustomCode.useSetValue();

  const DropDownHolder = CustomCode.DropDownHolder;

  // const Nophoto = Images.Nophoto;
  // ["Ninguno","Vehiculo","Moto","Bici","Publico"]

  // setVariable({ key: 'loading', value: true });
  const { theme } = props;

  const requestVacPOST = DcSSApi.useRequestVacPOST();

  const isFocused = useIsFocused();
  React.useEffect(() => {
    try {
      if (!isFocused) {
        return;
      }
      initScreenVars();
      setDateFrom(props.route?.params?.dateFrom ?? '');
      setDateTo(props.route?.params?.dateTo ?? '');
    } catch (err) {
      console.error(err);
    }
  }, [isFocused]);

  const [dateFrom, setDateFrom] = React.useState(new Date());
  const [dateFromError, setDateFromError] = React.useState('');
  const [dateTo, setDateTo] = React.useState(new Date());
  const [dateToError, setDateToError] = React.useState('');
  const [ioAction, setIoAction] = React.useState('');
  const [loading, setLoading] = React.useState(false);
  const [screenName, setScreenName] = React.useState('Vacaciones');

  return (
    <ScreenContainer scrollable={false} hasSafeArea={true}>
      <Utils.CustomCodeErrorBoundary>
        <CustomCode.DigicardScreenHeader
          {...props}
          right={{
            icon: 'Ionicons/home-outline',
            nav: 'Main',
            screen: 'InicioScreen',
          }}
          title={screenName}
        />
      </Utils.CustomCodeErrorBoundary>
      <CustomCode.LoadingOverlay
        {...props}
        visible={loading}
        action={ioAction}
      />

      <View style={styles.Viewnb}>
        <Image
          style={styles.ImageIm}
          resizeMode={'contain'}
          source={Images.UndrawAircraftFbvl}
        />
      </View>

      <View style={[styles.ViewUc, { borderRadius: 12 }]}>
        <View
          style={[styles.ViewUQ, { backgroundColor: theme.colors.secondary }]}
        >
          <Text style={[styles.TextkK, { color: theme.colors.background }]}>
            {'Período Solicitado'}
          </Text>
        </View>

        <View style={styles.Viewho}>
          <View>
            <>
              {!!(props.route?.params?.readOnly ?? false) ? null : (
                <DatePicker
                  onDateChange={newDpFromValue => {
                    try {
                      setDateFrom(newDpFromValue);
                    } catch (err) {
                      console.error(err);
                    }
                  }}
                  style={styles.DatePickerpc}
                  label={'Desde'}
                  date={dateFrom}
                  mode={'date'}
                  leftIconMode={'inset'}
                  type={'solid'}
                  disabled={false}
                />
              )}
            </>
            <>
              {!(props.route?.params?.readOnly ?? false) ? null : (
                <DatePicker
                  style={styles.DatePickerYc}
                  label={'Desde'}
                  date={dateFrom}
                  mode={'date'}
                  leftIconMode={'inset'}
                  type={'solid'}
                  disabled={true}
                />
              )}
            </>
            <>
              {!dateFromError ? null : (
                <Text
                  style={[styles.TextmP, { color: theme.colors.error }]}
                  numberOfLines={1}
                >
                  {dateFromError}
                </Text>
              )}
            </>
          </View>

          <View>
            <>
              {!!(props.route?.params?.readOnly ?? false) ? null : (
                <DatePicker
                  onDateChange={newDpToValue => {
                    try {
                      setDateTo(newDpToValue);
                    } catch (err) {
                      console.error(err);
                    }
                  }}
                  style={styles.DatePicker_56}
                  label={'Hasta'}
                  date={null}
                  mode={'date'}
                  leftIconMode={'inset'}
                  type={'solid'}
                />
              )}
            </>
            <>
              {!(props.route?.params?.readOnly ?? false) ? null : (
                <DatePicker
                  style={styles.DatePickergc}
                  label={'Hasta'}
                  date={dateTo}
                  mode={'date'}
                  leftIconMode={'inset'}
                  type={'solid'}
                  disabled={true}
                />
              )}
            </>
            <>
              {!dateToError ? null : (
                <Text
                  style={[styles.Textgb, { color: theme.colors.error }]}
                  numberOfLines={1}
                >
                  {dateToError}
                </Text>
              )}
            </>
          </View>
        </View>
      </View>

      <View style={styles.ViewQJ}>
        <>
          {!!(props.route?.params?.readOnly ?? false) ? null : (
            <ButtonSolid
              onPress={async () => {
                try {
                  const validateError = validate();
                  if (validateError) {
                    return;
                  }
                  setIoAction('saving');
                  setLoading(true);
                  const fetchResult = await requestVacPOST.mutateAsync({
                    emp_id: Constants['my_emp_id'],
                    req_datetime: now(),
                    req_vac_from: Date2IsoDateString(dateFrom),
                    req_vac_to: Date2IsoDateString(dateTo),
                  });
                  setLoading(false);
                  const isError = postResultToast(fetchResult);
                } catch (err) {
                  console.error(err);
                }
              }}
              style={[
                styles.ButtonSolidRI,
                {
                  backgroundColor: theme.colors.primary,
                  color: theme.colors.background,
                },
              ]}
              title={'Solicitar'}
            />
          )}
        </>
      </View>
    </ScreenContainer>
  );
};

const styles = StyleSheet.create({
  ImageIm: {
    height: 250,
    opacity: 0.75,
    width: '100%',
  },
  Viewnb: {
    alignItems: 'center',
  },
  TextkK: {
    fontFamily: 'System',
    fontWeight: '500',
    fontSize: 18,
  },
  ViewUQ: {
    justifyContent: 'center',
    alignItems: 'center',
    marginTop: 5,
    marginBottom: 5,
    height: 45,
  },
  DatePickerpc: {
    marginTop: 5,
  },
  DatePickerYc: {
    marginTop: 5,
  },
  TextmP: {
    fontSize: 10,
    marginBottom: 1,
    textAlign: 'center',
  },
  DatePicker_56: {
    marginTop: 5,
  },
  DatePickergc: {
    marginTop: 5,
  },
  Textgb: {
    fontSize: 10,
    marginBottom: 1,
    textAlign: 'center',
  },
  Viewho: {
    marginTop: 5,
    marginBottom: 5,
    marginLeft: '3%',
    marginRight: '3%',
  },
  ViewUc: {
    marginLeft: '3%',
    marginRight: '3%',
  },
  ButtonSolidRI: {
    borderRadius: 10,
    fontFamily: 'System',
    fontWeight: '700',
    textAlign: 'center',
    marginLeft: '6%',
    marginRight: '6%',
    minHeight: 48,
  },
  ViewQJ: {
    justifyContent: 'center',
    marginLeft: '3%',
    marginRight: '3%',
    marginTop: 25,
  },
});

export default withTheme(SolicitudVacacionesScreen);
